.apex-home {
  color: white;
  min-height: 100vh;
  
  .document {
    padding: 8rem 0;

    .top-half,
    .bottom-half {
      cursor: pointer;
      overflow: hidden;
      background-color: rgba(255, 255, 255, 0.08);
      height: 24rem;
      margin: 2rem 0;
      display: flex;
      flex-direction: row; // Default layout for larger screens

      .text {
        padding: 2rem;
        flex: 1; // Take up equal space
      }

      h1 {
        margin: 0;
      }

      ul {
        margin: 0;

        li {
          margin: 1rem 0;
        }
      }

      img {
        height: 100%;
        width: 50%;
        object-fit: cover;
      }
    }

    .top-half {
      border-top-left-radius: 2rem;
      border-top-right-radius: 2rem;
      margin-right: 4rem;

      img {
        object-fit: cover;
        width: 100%;
        left: 10vw;
        position: absolute;
        clip-path: polygon(1rem 0, 100% 0, 100% 100%, 0 100%);
      }
    }

    .bottom-half {
      border-bottom-right-radius: 2rem;
      border-bottom-left-radius: 2rem;
      margin-left: 4rem;

      img {
        clip-path: polygon(0 0, 100% 0, calc(100% - 1rem) 100%, 0 100%);
      }

      ul {
        direction: rtl;
      }
    }

    .fade-image {
      position: relative;
      width: 100%;
      height: 100%;
      object-fit: cover;

      .fade-img {
        position: relative;
        object-fit: cover;
      }
    }
  }
  
  @media (max-width: 768px) {
    .document {
      padding: 1rem;

      .bottom-half {
        flex-direction: column; // Change to column layout on smaller screens
        height: auto; // Adjust height to auto for vertical stacking
        margin: 1rem 0; // Adjust margin for better spacing

        .text {
          max-width: 100%;
          padding: 1rem;
        }
        
        
        img {
          width: 100%; // Full width for images
          height: auto; // Adjust height to auto
          clip-path: none; // Remove clip-path for simplicity
          // position: absolute;
        }
        
      }
      .top-half {
        flex-direction: column;
        height: 40rem;
        width:100%;
        border-top-left-radius: 2rem;
        border-top-right-radius: 2rem;
        // margin-right: 4rem;
  
        img {
          object-fit: cover;
          width: 100%;
          height: 100%;
          left: 0vw;
          position: absolute;
          clip-path: none;
        }
      }
    }
  }
}

.apex-coding,
.apex-consulting {
  color: white;
  min-height: 100vh;
  padding: 8rem 0 2rem;

  .document {
    background-color: rgba(255, 255, 255, 0.08);
    border-radius: 2rem;
    overflow: hidden;

    > *:not(:first-child) {
      margin-top: 2rem;
    }

    .explanation {
      padding: 0 2rem;
    }

    .text {
      padding: 2rem;
    }

    .h0 {
      letter-spacing: 1px;
      margin: 0;
      text-shadow: 0 0.25rem 0.5rem black;
      cursor: pointer;
      background-color: rgba(255, 255, 255, 0.08);
      padding: 2rem;

      .back {
        position: absolute;
        left: 2rem;
        padding: 0 1rem;
        border-radius: 1rem;
      }
    }

    .list {
      padding: 0 2rem;
    }

    ul {
      margin: 0;
      li {
        margin: 1rem 0;
      }
    }

    iframe {
      width: 100%; /* Adjusted for responsiveness */
      aspect-ratio: 3/2;
      border: none; /* Ensure no border */
      margin-top: 1rem; /* Added margin top for spacing */
    }

    .click-to {
      cursor: pointer;
      background-color: rgba(255, 255, 255, 0.08);
      padding: 2rem;
      border-bottom-left-radius: 1rem;
      border-bottom-right-radius: 1rem;
    }

    img {
      width: 100%; /* Adjusted for responsiveness */
      max-width: 18rem; /* Added max-width to limit size on larger screens */
      height: auto; /* Maintain aspect ratio */
      border-radius: 2rem;
      margin-top: 1rem; /* Added margin top for spacing */
    }

    .swiper-button-prev {
      background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg%20xmlns%3D'http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg'%20viewBox%3D'0%200%2027%2044'%3E%3Cpath%20d%3D'M0%2C22L22%2C0l2.1%2C2.1L4.2%2C22l19.9%2C19.9L22%2C44L0%2C22L0%2C22L0%2C22z'%20fill%3D'%234c71ae'%2F%3E%3C%2Fsvg%3E") !important;
    }
  }

  @media (max-width: 768px) {
    .document {
      padding: 1rem; /* Reduce padding on smaller screens */
    }

    .h0 {
      font-size: 2rem; /* Decrease font size on smaller screens */
    }

    .list {
      flex-direction: column; /* Stack list items vertically on smaller screens */
    }

    iframe {
      width: 100%; /* Make iframe full width on smaller screens */
    }

    img {
      width: 100%; /* Make images full width on smaller screens */
      max-width: none; /* Remove max-width constraint */
    }
  }
}


