.apex-test {
  color: white;
  min-height: 100vh;
  padding: 8rem 1rem 2rem; // Changed to 1rem to add padding on smaller screens

  .document {
    background-color: rgba(255, 255, 255, 0.08);
    border-radius: 2rem;
    overflow: hidden;
    width: 100%; // Changed to 100% for responsive width
    max-width: 48rem; // Added max-width for larger screens
    > *:not(:first-child) {
      margin-top: 2rem;
    }

    .explanation {
      padding: 0 2rem;
    }

    .h0 {
      letter-spacing: 1px;
      margin: 0;
      text-shadow: 0 0.25rem 0.5rem black;
      cursor: pointer;
      background-color: rgba(255, 255, 255, 0.08);
      padding: 2rem;

      .back {
        position: absolute;
        left: 2rem;
        padding: 0 1rem;
        border-radius: 1rem;
      }
    }

    .list {
      padding: 0 2rem;
    }

    ul {
      list-style-type: none;
      margin: 0;
      li {
        margin: 1rem 0;
      }
    }

    iframe {
      width: 100%; // Changed to 100% for responsive width
      aspect-ratio: 3/2;
    }

    .click-to {
      cursor: pointer;
      background-color: rgba(255, 255, 255, 0.08);
      padding: 2rem;
      border-bottom-left-radius: 1rem;
      border-bottom-right-radius: 1rem;
    }

    .profile {
      text-align: center;
      width: 1rem;
      height: 1rem;
      border-radius: 50%;
    }

    img {
      width: 100%; // Changed to 100% for responsive width
      max-width: 18rem; // Added max-width for larger screens
      height: auto; // Changed to auto for responsive height
      border-radius: 50%;
    }

    .name {
      position: relative;
      font-size: 2rem; // Changed to a more responsive unit
      width: 100%; // Changed to 100% for responsive width
      max-width: 18rem; // Added max-width for larger screens
      text-align: justify;
    }

    .caption {
      position: relative;
      width: 100%; // Changed to 100% for responsive width
      max-width: 18rem; // Added max-width for larger screens
      text-align: justify;
    }

    .test {
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .test2 {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 2rem;
    }

    input:-webkit-autofill,
    input:-webkit-autofill:hover,
    input:-webkit-autofill:focus,
    input:-webkit-autofill:active {
      transition: background-color 5000s ease-in-out 0s;
    }

    input:-webkit-autofill {
      -webkit-text-fill-color: white !important;
    }
  }
}

// Media Queries for smaller screens
@media (max-width: 768px) {
  .apex-test {
    padding: 4rem 1rem 1rem; // Adjusted padding for smaller screens
  }

  .document {
    padding: 1rem;
    border-radius: 1rem; // Adjusted border-radius for smaller screens
  }

  .h0 {
    padding: 1rem;
  }

  .click-to {
    padding: 1rem;
  }
}

